.nav-wrapper {
  height: 10vh;
  border: 1px solid black;
  display: flex;
  padding: 0 100px;
  background-color: bisque;
}
.nav-left {
  flex: 1;
  border: 1px solid red;
  align-items: center;
}
.nav-right {
  flex: 1;
  border: 1px solid red;
  align-items: center;
}
.nav-wrapper > ul {
  display: flex;
  gap: 40px;
}
