.about-wrapper {
  border: 2 px solid green;
  background-color: rgb(195, 161, 26);
  height: 77vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  /* text-align: center; */
}
.about-heading {
  border: 1px solid green;
  text-align: center;
}

.about-heading > h1 {
  font-size: 50px;
}
.about-l-r {
  border: 1px solid blue;
  display: flex;
  padding: 70px;
}

.about-wrap-left {
  flex: 1;
  border: 1px solid yellow;
  display: flex;
  align-items: center;
  justify-content: center;
}
.about-wrap-right {
  flex: 1;
  border: 1px solid yellow;
  padding: 30px;
}


