.home-wrapper{
    background-color: cadetblue;
    display: flex;
    height: 77vh;
    border: 1px solid green;
  
}

.home-left{
    flex: 1;
    border: 1px solid blue;
    padding: 10px;
    display: flex;
    align-items: center;

    justify-content: center;
    
    
}

.home-right{
    flex: 1;
    border: 1px solid blue;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
   
    
}
.h-left-wrapper{
    border: 1px solid red;
    padding: 50px;
    display: flex;
    flex-direction: column;
   
   
}
.h-left-wrapper > span:nth-child(1){
    font-size: 50px;
}
.h-left-wrapper > span:nth-child(2){
    font-size: 50px;
}

.h-right-wrapper{
    border: 1px solid red;
    display: flex;
    align-items: center;
    justify-content: center;
    

}

.h-left-logos{
    margin-top: 10px;
}